import { Injectable } from '@angular/core';
import { NgbDateStruct, NgbDatepickerI18n } from '@ng-bootstrap/ng-bootstrap';

const I18N_VALUES = {
  weekdays: ['Ø¥Ø«', 'Ø«Ù', 'Ø£Ø±', 'Ø®Ù', 'Ø¬Ù', 'Ø³Ø¨', 'Ø£Ø­'],
  months: ['ÙØ­Ø±Ù', 'ØµÙØ±', 'Ø±Ø¨ÙØ¹ Ø§ÙØ£ÙÙ', 'Ø±Ø¨ÙØ¹ Ø§ÙØ¢Ø®Ø±', 'Ø¬ÙØ§Ø¯Ù Ø§ÙØ£ÙÙÙ', 'Ø¬ÙØ§Ø¯Ù Ø§ÙØ¢Ø®Ø±Ø©', 'Ø±Ø¬Ø¨', 'Ø´Ø¹Ø¨Ø§Ù', 'Ø±ÙØ¶Ø§Ù', 'Ø´ÙØ§Ù',
    'Ø°Ù Ø§ÙÙØ¹Ø¯Ø©', 'Ø°Ù Ø§ÙØ­Ø¬Ø©']
};

@Injectable({ providedIn: 'root' })
export class IslamicI18n extends NgbDatepickerI18n {
  /** @description Gets the weekday short name.
   * @param number weekday The weekday.
   * @return string The weekday short name.
  */
  public getWeekdayShortName(weekday: number): string {
    return I18N_VALUES.weekdays[weekday - 1];
  }

  /** @description Gets the weekday short name.
   * @param number month The month.
   * @return string The month short name.
  */
  public getMonthShortName(month: number): string {
    return I18N_VALUES.months[month - 1];
  }

  /** @description Gets the month full name.
   * @param number month The month.
   * @return string The month full name.
  */
  public getMonthFullName(month: number): string {
    return this.getMonthShortName(month);
  }

  /** @description Gets the day aria label.
   * @param NgbDateStruct date The date.
   * @return string The day aria label.
  */
  public getDayAriaLabel(date: NgbDateStruct): string {
    return `${date.day}-${date.month}-${date.year}`;
  }
}
